html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  background-color: #2e273d;
  color: #ffffff;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0;
  line-height: 1.3;
}

h2 {
  margin: 0;
  line-height: 1.3;
}

h3 {
  margin: 0;
  line-height: 1.3;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.main {
  border-radius: 1rem;
  padding: 5rem 0;
}

.budget__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .budget__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.budget__cards {
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.budget-form {
  border-radius: 1rem;
  padding: 2rem;
  display: grid;
  gap: 0.8rem;
  background-color: #5d4f7b;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.budget-form__field {
  display: flex;
  flex-direction: column;
}
.budget-form__label {
  margin-left: 1rem;
  margin-bottom: 0.5rem;
  font-weight: 500;
  font-size: 1.6rem;
}
.budget-form__input {
  outline: none;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
  font-size: 1.8rem;
  font-weight: 400;
  color: #ffffff;
  background-color: #453b5c;
}
.budget-form__btn {
  margin-top: 2rem;
  border: none;
  border-radius: 1rem;
  outline: none;
  display: block;
  padding: 1rem 2rem;
  background-image: linear-gradient(90deg, rgb(215, 134, 255) 30%, rgb(46, 39, 61) 100%);
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
}
@media (hover: hover) {
  .budget-form__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .budget-form__btn:hover {
    cursor: pointer;
  }
}

.budget-card {
  border-radius: 1rem;
  padding: 2rem;
  background-color: #5d4f7b;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.budget-card:first-child {
  grid-column: 1/3;
}
.budget-card__text {
  font-size: 1.6rem;
  font-weight: 400;
  color: #c9c9c9;
}
.budget-card__text span {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
}